import { useHttp } from "../hooks/http.hook";

const useMarvelService = () => {
  const {loading, request, error, clearError} = useHttp();

  const _apiBase = 'https://gateway.marvel.com:443/v1/public/';
  const _apiKey = 'apikey=0cabaa5c82f123bb2173e8576631d568';
  const _baseOffset = 210;
  const _comicsOffset = 10500;

  const getAllCharacters = async (offset = _baseOffset) => {
    const res = await request(`${_apiBase}characters?limit=9&offset=${offset}&${_apiKey}`);
    return res.data.results.map(_transformCharacter);
  }

  const getCharacter = async (id) => {
    const res = await request(`${_apiBase}characters/${id}?${_apiKey}`);
    return _transformCharacter(res.data.results[0]);
  }

  const getAllComics = async (offset = _comicsOffset) => {
    const res = await request(`${_apiBase}comics?limit=8&offset=${offset}&${_apiKey}`);
    return res.data.results.map(_transformComics);
  }

  const getComic = async (id) => {
    const res = await request(`${_apiBase}comics/${id}?${_apiKey}`);
    return _transformComics(res.data.results[0]);
  }

  const getItem = async (id, item) => {
    let res;
    if (item === 'char') {
      res = await request(`${_apiBase}characters/${id}?${_apiKey}`);
      return _transformCharacter(res.data.results[0]);
    }
    else if (item === 'comic') {
      res = await request(`${_apiBase}comics/${id}?${_apiKey}`);
      return _transformComics(res.data.results[0]);
    }
    
  }
  const _transformCharacter = (char) => {
    return {
      id: char.id,
      name: char.name,
      description: char.description ? `${char.description.slice(0, 210)}...` : 'There is no description for this character',
      thumbnail: char.thumbnail.path + '.' + char.thumbnail.extension,
      homapage: char.urls[0].url,
      wiki: char.urls[1].url,
      comics: char.comics.items,
    }
  }

  const _transformComics = (comic) => {
    return {
      id: comic.id,
      title: comic.title,
      price: comic.digitalId && comic.prices[0].price ? comic.prices[0].price + '$' : 'NOT AVAILABLE',
      thumbnail: comic.thumbnail.path + '.' + comic.thumbnail.extension,
      description: comic.description || 'There is no description',
      language: comic.textObjects.language || 'en-us',
      pageCount: comic.pageCount ? `${comic.pageCount} p.` : 'No information about the number of pages'
    }
  }
  return {loading, error, getAllCharacters, getCharacter, clearError, getAllComics, getComic, getItem}
}


export default useMarvelService;