import { useFormik } from "formik";
import { useState, useRef } from "react";
import { Link } from "react-router-dom";
import * as Yup from 'yup';

import './charForm.scss';


const CharForm = () => {

  const [char, setChar] = useState({});
  const [isError, setIsError] = useState(false);

  const input = useRef();

  const formik = useFormik({
    initialValues: {
      char: ''
    },
    validationSchema: Yup.object({
      char : Yup.string()
              .required('This field is required')
              .min(3, 'Minimum 3 letters are required...')
    }),
    onSubmit: values => {
      fetch(`https://gateway.marvel.com:443/v1/public/characters?name=${values.char.trim()}&apikey=0cabaa5c82f123bb2173e8576631d568`)
        .then(data => data.json())
        .then(data => {
          if (data.data.results.length === 0) {
            throw Error;
          }
          return {
            id: data.data.results[0].id,
            name: data.data.results[0].name,
            description: data.data.results[0].description ? data.data.results[0].description : 'There is no description',
            thumbnail: data.data.results[0].thumbnail.path + '.' + data.data.results[0].thumbnail.extension
          }
        })
        .then(data => {
          setChar(data);
          setIsError(false);
        })
        .catch(() => {
          setIsError(true);
        });
    }
  })

  // console.log(isError)
  // console.log(char)
  console.log('render form')
  return (
    <>
      <form className='char__form' onSubmit={formik.handleSubmit}>
        <label htmlFor="char">
          Or find a character by name:
        </label>
        <div className="char__form-items">
          <input ref={input}
            type="text" 
            id='char' 
            name="char" 
            value={formik.values.char}
            placeholder='Enter name'
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            />
          <button className='button button__main' type='submit'>
            <div className="inner">FIND</div>
          </button>
        </div>
        {!isError && Object.keys(char).length > 0 ? 
          <div className="response">
            <div className="response__title">There is! Visit {char.name} page?</div>
            <Link to={`/marvel/characters/${char.id}`} className='button button__main' type='submit'>
              <div className="inner">TO PAGE</div>
            </Link>
          </div> : null}
        {isError ? 
          <div className="response__red">
            The character was not found. Check the name and try again
          </div> : null}
        {formik.errors.char && formik.touched.char ? <div className="error">{formik.errors.char}</div> : null}
      </form>
    </>
  )
}

export default CharForm;